@import "node_modules/bootstrap/less/variables.less";
@import "node_modules/bootstrap/less/mixins.less";
@import "node_modules/font-awesome/less/variables.less";
@import "src/less/variables.less";
@import "src/less/mixins.less";

@navbar-sidebar-bg-color:           #292e33;
@navbar-sidebar-width:              240px;
@navbar-toggle-sidebar-width:       25px;

/* CUSTOM SHOWCASE STYLES */

#fixed-accordion {
  width: 240px;
}

.panel-body {
  height: 200px;
  overflow: scroll;
}

.layout-pf.layout-pf-fixed .navbar-pf {
  position: relative;
  z-index: 1;
}

.layout-pf-fixed .navbar-pf-vertical {
  position: absolute;
  z-index: 1;
}

.nav-pf-vertical {
  position: absolute;
  z-index: 1;
}

.bootstrap-switch .bootstrap-switch-handle-on,
.bootstrap-switch .bootstrap-switch-label,
.bootstrap-switch .bootstrap-switch-handle-off,
.bootstrap-switch .bootstrap-switch-label {
   height: inherit;
}

.doc-wrapper {
  padding: 74px 0;
}

.doc-header {
  margin-bottom: 30px;
}

.doc-header .doc-title {
  color: #39a5dc;
  font-size: 36px;
}

.doc-body {
  position: relative;
}

.doc-content {
  margin-left: 230px;
}

@media (max-width: 767px) {
  .doc-content {
    margin-left: 0px;
  }
}

.doc-sidebar {
  width: 200px;
}

.affix-top {
  position: absolute;
  top: 23px;
}

.doc-menu {
  list-style: none;
  padding-left: 0;
  margin-top: 20px;
}

.doc-menu > li {
  margin-bottom: 5px;
}

.section-block {
  padding-top: 10px;
  padding-bottom: 10px;
}

pre {
  max-height: 200px;
  overflow: auto;
}

pre code {
  overflow: auto;
  white-space: pre;
}

.doc-section {
  padding-top: 15px;
  padding-bottom: 15px;
}

.doc-example {
  position: relative;
  padding: 4em 1em 1em;
  border: 1px solid #d8d8d8;
  margin: 1.5rem 0 0;
}

.doc-example::before {
  position: absolute;
  left: 0;
  top: 0;
  padding: 1em;
  color: #aaa;
  font-size: smaller;
  font-weight: 500;
  content: 'EXAMPLE';
}

.doc-example.char-count {
  padding-bottom: 3em;
}

.content-view-pf-pagination .form-group:last-child {
  align-items: center;
}

/* DEFAULT SHOWCASE STYLES */
.showcase {
header {
 + .container-fluid {
    padding-bottom: 50px; // Sticky footer
